.Screen {
  border-radius: 3vh;
  background: rgb(233, 26, 88);
  margin: 0 auto;
  width: calc(100%-2vh);
  height: 45%;
  z-index: 0;
  position: relative;
}
.Screen2 {
  background: rgb(25, 25, 25);
  padding: 2%;
  padding-bottom: 5%;
  z-index: 0;
  margin-left: 6.5%;
  margin-top: 7%;
  width: 80%;
  height: 65%;
  position: absolute;

  border-top: 0.5vh rgb(134, 0, 40) solid;
  border-right: 0.5vh rgb(255, 149, 181) solid;
  border-left: 0.5vh rgb(134, 0, 40) solid;
  border-bottom: 0.5vh rgb(255, 149, 181) solid;
}
.Screen3 {
  border-radius: 10px;
  position: relative;
  margin: 0 auto;

  width: 100%;
  height: 100%;
  background: green;
  z-index: 0;
}

.Logo {
  display: block;
  width: 50%;
  margin: 0 auto;
}
